* {
	outline: none;
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
}

html,
body {
	margin: 0;
    padding: 0;
}


body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
        "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
        "Segoe UI Symbol";
}

#page {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
}
